<template>
  <aside :class="`${is_expanded ? 'is-expanded' : ''}`">
    <div class="logo">
      <img :src="logoURL" alt="Animal House logo" />
    </div>

    <div class="menu-toggle-wrap">
      <button class="menu-toggle" @click="ToggleMenu">
        <span class="material-icons">keyboard_double_arrow_right</span>
      </button>
    </div>

    <h3>AnimalHouse</h3>
    <div class="menu">
      <router-link class="button" to="/">
        <span class="material-icons">home</span>
        <span class="text">Home</span>
      </router-link>

      <router-link class="button" to="/curiosity">
        <span class="material-icons">cruelty_free</span>
        <span class="text">Curiosity</span>
      </router-link>

      <router-link class="button" to="/animalQuiz">
        <span class="material-icons">pets</span>
        <span class="text">Quiz</span>
      </router-link>

      <router-link class="button" to="/memory">
        <span class="material-icons">quiz</span>
        <span class="text">Memory</span>
      </router-link>

      <router-link class="button" to="/videos">
        <span class="material-icons">play_arrow</span>
        <span class="text">Videos</span>
      </router-link>

      <router-link class="button" to="/facts">
        <span class="material-icons">lightbulb</span>
        <span class="text">Facts</span>
      </router-link>

      <router-link class="button" to="/about">
        <span class="material-icons">visibility</span>
        <span class="text">About</span>
      </router-link>

      <router-link v-if="!user" class="button" to="/login">
        <span class="material-icons">login</span>
        <span class="text">login</span>
      </router-link>

      <router-link v-if="!user" class="button" to="/signup">
        <span class="material-icons">person</span>
        <span class="text">signUp</span>
      </router-link>

      <router-link v-if="user" class="button" to="/logout">
        <span class="material-icons">logout</span>
        <span class="text">logout</span>
      </router-link>
    </div>
  </aside>
</template>

<script>
import {ref} from 'vue'
import logoURL from '../icons/cano.png'
import {mapGetters} from "vuex";

export default{
  name:'vueSidebar',
  computed:{
    ...mapGetters(['user'])
  },
  setup(){
    const is_expanded = ref(localStorage.getItem("is_expanded") === "true")
    const ToggleMenu = () => {
      is_expanded.value = !is_expanded.value
      localStorage.setItem("is_expanded", is_expanded.value)
    }
    return{
      ToggleMenu,
      logoURL,
      is_expanded
    }
  }
}
</script>

<style lang="scss" scoped>
aside {
  display: flex;
  flex-direction: column;
  background-color: var(--dark);
  color: var(--light);
  width: calc(2rem + 32px);
  overflow: hidden;
  min-height: 100vh;
  padding: 1rem;
  transition: 0.2s ease-in-out;
  .flex {
    flex: 1 1 0%;
  }
  .logo {
    margin-bottom: 1rem;
    img {
      width: 2rem;
    }
  }
  .menu-toggle-wrap {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
    position: relative;
    top: 0;
    transition: 0.2s ease-in-out;
    .menu-toggle {
      transition: 0.2s ease-in-out;
      .material-icons {
        font-size: 2rem;
        color: var(--light);
        transition: 0.2s ease-out;
      }

      &:hover {
        .material-icons {
          color: var(--primary);
          transform: translateX(0.5rem);
        }
      }
    }
  }
  h3, .button .text {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  h3 {
    color: var(--primary);
    font-size: 0.875rem;
    margin-bottom: 0.5rem;
    text-transform: uppercase;
  }
  .menu {
    margin: 0 -1rem;
    .button {
      display: flex;
      align-items: center;
      text-decoration: none;
      transition: 0.2s ease-in-out;
      padding: 0.5rem 1rem;
      .material-icons {
        font-size: 2rem;
        color: var(--light);
        transition: 0.2s ease-in-out;
      }
      .text {
        color: var(--light);
        transition: 0.2s ease-in-out;
      }
      &:hover {
        background-color: var(--dark-alt);
        .material-icons, .text {
          color: var(--primary);
        }
      }
      &.router-link-exact-active {
        background-color: var(--dark-alt);
        border-right: 5px solid var(--primary);
        .material-icons, .text {
          color: var(--primary);
        }
      }
    }
  }
  .footer {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    p {
      font-size: 0.875rem;
      color: var(--grey);
    }
  }
  &.is-expanded {
    width: var(--sidebar-width);
    .menu-toggle-wrap {
      top: -3rem;

      .menu-toggle {
        transform: rotate(-180deg);
      }
    }
    h3, .button .text {
      opacity: 1;
    }
    .button {
      .material-icons {
        margin-right: 1rem;
      }
    }
    .footer {
      opacity: 0;
    }
  }
  @media (max-width: 1024px) {
    position: fixed;
    z-index: 99;
  }
}
</style>